



























































import { Component, Vue, Prop } from 'vue-property-decorator'
import axios from 'axios'
import { Apis } from '@/api/index.ts'
import DqFilter from '@/components/DqFilter.vue'
import ProjectSignup from '@/components/ProjectSignup.vue'
import ProjectManagement from '@/components/ProjectManagement.vue'

@Component({
  components: {
    DqFilter,
    ProjectSignup,
    ProjectManagement
  }
})

export default class Info extends Vue {
  @Prop({
    type: String,
    default: ()=>{
      return 'student'
    }
  }) type!: string

  filterConfig = {
    width: '90%',
    items: {
      team: {
        label: '导师团队',
        placeholder: '请输入导师团队',
        type: 'input',
        value: '',
        submitkey: 'team',
      },
      academy: {
        label: '所属学院',
        placeholder: '请输入所属学院',
        type: 'input',
        value: '',
        submitkey: 'academy',
      },
    },
    buttons: {
      submit: {
        text: '查询',
        func: (data: any)=> {
          this.getProjectList()
          this.print()
          console.log('this1',this)
        }
      }
    }
  }
  tableData= []

  pagination = {
    current: 1,
    size: 3,
    total: 1
  }

  // 抽屉中的内容展示

  drawer = false

  details = ''

  print(){
    console.log('能否调用this')
  }

  getProjectList() {
    axios.get(Apis.searchproject, {
      params: {
        team: this.filterConfig.items.team.value,
        academy: this.filterConfig.items.academy.value,
        page: this.pagination.current,
        limit: this.pagination.size
      }
    }).then((resp: any)=>{
      const data = resp && resp.data && resp.data.Data
      const projects = data && data.projects
      this.tableData = projects
      this.pagination.total = data && data.count || 10

    })
  }

  pageChange(){
    console.log("sss", this.pagination.current)
  }

  handleSizeChange(val: number) {
    console.log(`每页 ${val} 条`);
  }

  handleCurrentChange(val: number) {
    console.log(`当前页: ${val}`);
    this.pagination.current = val
    this.getProjectList()
  }

  mounted(){
    this.getProjectList()
  }
}
